import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import Loader from "components/Loader";
import { dateToString } from "helpers/datetimeCover";

const NotificationDetail = (props) => {
  const [loading, setLoading] = React.useState(false);
  const getIcon = () => {
    let icon;
    let color;
    let title;
    switch (props.content.type) {
      case "WARRING_LOW_TEMPERATURE":
        icon = "uil  uil-temperature-minus";
        color = "warning";
        title = "Cnh bo nhit  thp";
        break;
      case "WARRING_HIGH_TEMPERATURE":
        icon = "uil  uil-temperature-plus";
        color = "danger";
        title = "Cnh bo nhit  cao";
        break;
      case "Access-Invite":
        icon = "uil uil-envelope-add";
        color = "success";
        title = "Li mi cng tc";
        break;
      case "SUCCESS":
        icon = "uil uil-check";
        color = "success";
        title = "Thnh cng";
        break;
      case "ERRO":
        icon = "uil uil-times";
        color = "danger";
        title = "Li";
        break;

      default:
        icon = "uil  uil-exclamation-triangle";
        color = "warning";
        title = "Thng bo";
        break;
    }
    return {
      icon: icon,
      color: color,
      title: title,
    };
  };
  return (
    <Modal
      isOpen={props.isOpen}
      toggle={props.toggle}
      size="md"
      className="modal-dialog-centered"
    >
      {loading && <Loader />}
      <ModalHeader toggle={props.toggle}>
        <i className={getIcon().icon + " mr-2"}></i>
        {getIcon().title}
      </ModalHeader>
      <ModalBody>
        <h5>{props.content.content}</h5>
        <small className="text-muted mt-4">
          {dateToString(props.content.updatedAt)}
        </small>
      </ModalBody>
      <ModalFooter className="text-right">
        {props.content.type === "Access-Invite" ? (
          <>
            <Button color="primary" onClick={props.sayYesAccess}>
              Chp nhn
            </Button>
            <Button
              color="secondary"
              onClick={props.sayNoAccess}
              className="ml-1"
            >
              T chi
            </Button>
          </>
        ) : (
          <>
            <Button color="danger" onClick={props.deleteAction}>
              Xa thng bo
            </Button>
            <Button color="secondary" className="ml-1" onClick={props.toggle}>
              ng
            </Button>
          </>
        )}
      </ModalFooter>
    </Modal>
  );
};
export default NotificationDetail;
